html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: @font;
  font-size: @font-size;
  line-height: @line-height;
  color: @font-color;
  background-color: @white;
}

figure {
  margin: 0;
}

fieldset {
  padding: 0;
}

blockquote {
  margin: 0;
}

img {
  max-width: 100%;
  height: auto;
}
