html {
  box-sizing: border-box;
}
*,
*:before,
*:after {
  box-sizing: inherit;
}
body {
  margin: 0;
  padding: 0;
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 14px;
  line-height: 24px;
  color: #283645;
  background-color: #ffffff;
}
figure {
  margin: 0;
}
fieldset {
  padding: 0;
}
blockquote {
  margin: 0;
}
img {
  max-width: 100%;
  height: auto;
}
/*Open Sans Regular*/
@font-face {
  font-family: "Open Sans";
  src: url("source/fonts/opensans.woff2") format("woff2"), url("source/fonts/opensans.woff") format("woff");
  font-weight: 400;
  font-style: normal;
}
/*Open Sans Light*/
@font-face {
  font-family: "Open Sans";
  src: url("source/fonts/opensanslight.woff2") format("woff2"), url("source/fonts/opensanslight.woff") format("woff");
  font-weight: 300;
  font-style: normal;
}
/*Open Sans Bold*/
@font-face {
  font-family: "Open Sans";
  src: url("source/fonts/opensansbold.woff2") format("woff2"), url("source/fonts/opensansbold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
}
