@mixin visually-hidden() {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  height: 1px;
  width: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
}
@mixin reset-list() {
  margin: 0;
  padding: 0;
  list-style: none;
}
@mixin reset-child-margins() {
   > :first-child {
    margin-top: 0;
  }
   > :last-child {
    margin-bottom: 0;
  }
}
@mixin angled-bg(height: 25px, bgcolor: #f2f2f2) {
  overflow-x: hidden;
  ::before {
    content: "";
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 25px;
    border-top: 25px solid #ffffff;
    border-right: 100vw solid #f2f2f2;
    border-left: none;
  }
}
@mixin container() {
  width: 320px;
  padding-left: 20px;
  padding-right: 20px;
  margin: 0 auto;
  @media (min-width: 660px) {
    width: 660px;
  }
  @media (min-width: 1200px) {
    width: 960px;
    padding-right: 10px;
    padding-left: 10px;
  }
}
@mixin container-phones() {
  width: 320px;
  padding-left: 0;
  padding-right: 0;
  margin: 0 auto;
}
