/*sizes and breakpoints*/
.main-header {
  position: absolute;
  background-color: rgba(0, 0, 0, 0.3);
  color: #ffffff;
  width: 100%;
  z-index: 2;
}
.main-header__logo {
  height: 22px;
  display: inline-block;
  vertical-align: top;
  margin-top: 22px;
  margin-bottom: 22px;
  margin-left: 20px;
  margin-right: 20px;
}
@media (max-width: 959px) {
  .main-header--full {
    position: relative;
    background-color: #283645;
  }
}
@media (min-width: 660px) {
  .main-header {
    min-height: 110px;
  }
  .main-header__logo {
    height: 40px;
    margin-top: 27px;
    margin-bottom: 43px;
  }
  .main-header__logo img {
    padding-top: 17px;
  }
}
@media (min-width: 1200px) {
  .main-header__wrapper {
    display: flex;
  }
}
.main-nav__list {
  display: flex;
  flex-direction: column;
  text-align: center;
}
.main-nav--opened .main-nav__list {
  margin-top: 4px;
}
@media (min-width: 660px) {
  .main-nav--opened .main-nav__list {
    margin-top: 0;
  }
}
.main-nav__item a {
  display: block;
  margin: 0;
  background-color: #283645;
  color: #ffffff;
  text-decoration: none;
  text-transform: uppercase;
  font-size: 18px;
  font-weight: 700;
  line-height: 32px;
  padding: 16px 20px;
  box-shadow: 0 -1px 0 #000000, inset 0 1px 0 rgba(255, 255, 255, 0.15);
}
.main-nav__item a[href]:hover,
.main-nav__item a[href]:focus {
  color: #d22856;
}
.main-nav__item a[href]:active {
  color: rgba(255, 255, 255, 0.3);
}
@media (max-width: 959px) {
  .main-nav__item:last-child a {
    box-shadow: 0 -1px 0 #000000, inset 0 1px 0 rgba(255, 255, 255, 0.15), inset 0 -1px 0 #000000;
  }
}
.main-btn__toggle {
  position: absolute;
  top: 21px;
  right: 19px;
  width: 50px;
  height: 24px;
  padding: 0;
  background-color: transparent;
  border: none;
  cursor: pointer;
  outline: none;
}
.main-nav--closed .main-btn__toggle span {
  display: block;
  width: 50px;
  height: 4px;
  background-color: #ffffff;
  border-radius: 1px;
}
.main-nav--closed .main-btn__toggle span::before,
.main-nav--closed .main-btn__toggle span::after {
  content: "";
  position: absolute;
  left: 0;
  width: 50px;
  height: 4px;
  background-color: #ffffff;
  border-radius: 1px;
}
.main-nav--closed .main-btn__toggle span::before {
  top: 0;
}
.main-nav--closed .main-btn__toggle span::after {
  bottom: 0;
}
.main-nav--opened .main-btn__toggle span::before,
.main-nav--opened .main-btn__toggle span::after {
  content: "";
  position: absolute;
  right: 0;
  top: 10px;
  width: 29px;
  height: 4px;
  background-color: #ffffff;
  border-radius: 1px;
}
.main-nav--opened .main-btn__toggle span::before {
  transform: rotate(45deg);
}
.main-nav--opened .main-btn__toggle span::after {
  transform: rotate(-45deg);
}
.main-btn__toggle:hover span,
.main-btn__toggle:hover span::before,
.main-btn__toggle:hover span::after {
  background-color: #d22856;
}
.main-btn__toggle:active span,
.main-btn__toggle:active span::before,
.main-btn__toggle:active span::after {
  background: rgba(210, 40, 86, 0.3);
}
@media (max-width: 959px) {
  .main-nav--closed .main-nav__list {
    display: none;
  }
  .main-nav--nojs .main-btn__toggle {
    display: none;
  }
  .main-nav--nojs .main-nav__list {
    display: block;
  }
}
@media (min-width: 660px) {
  .main-btn__toggle {
    top: 44px;
  }
}
@media (min-width: 1200px) {
  .main-nav {
    margin-left: auto;
    width: 760px;
    padding-top: 35px;
    padding-bottom: 35px;
    flex-shrink: 1;
  }
  .main-btn__toggle {
    display: none;
  }
  .main-header__wrapper {
    @include container;
  }
  .main-nav__list {
    flex-wrap: wrap;
    flex-direction: row;
    border-top: none;
    justify-content: flex-end;
  }
  .main-nav__item {
    margin-left: 79px;
  }
  .main-nav__item:first-child {
    margin-left: 0;
  }
  .main-nav__item--active {
    position: relative;
  }
  .main-nav__item--active::after {
    content: "";
    position: absolute;
    right: 0;
    bottom: -2px;
    left: 0;
    height: 2px;
    background: #ffffff;
  }
  .main-nav__item a {
    background-color: transparent;
    border: none;
    box-shadow: none;
    text-transform: none;
    line-height: 18px;
    font-weight: 400;
    padding: 11px 0;
  }
}
