.download {
  padding-top: 28px;
  padding-bottom: 34px;
  background-color: @blue-darkest;
  text-align: center;

  @media (min-width: @tablets) {
    padding: 93px 0;
    width: 300px;
    position: relative;
    background-color: transparent;
    z-index: 2;
  }

  @media (min-width: @desktops) {
    padding: 109px 0;
  }
}

.download__inner {
  @media (max-width: @phones-max) {
    @include container-phones (20px);
  }
}

.download__slogan {
  @media (max-width: @phones-max) {
    @include visually-hidden;
  }

  @media (min-width: @tablets) {
    padding-left: 55px;
    padding-right: 55px;
    margin-top: 0;
    margin-bottom: 28px;
    font-size: 40px;
    line-height: 48px;
    font-weight: 300;
  }

  @media (min-width: @desktops) {
    padding-left: 30px;
    padding-right: 30px;
    margin-bottom: 51px;
    font-size: 50px;
    line-height: 60px;
  }
}

.download--button {
  width: 100%;
  margin-bottom: 36px;

  @media (min-width: @tablets) {
    margin-bottom: 46px;
  }

  @media (min-width: @desktops) {
    margin-bottom: 67px;
  }
}

.download__app {
  list-style: none;
  font-size: 0;
}

.app__btn {
  display: inline-block;
  width: 47px;
  height: 56px;
  background-color: @pink;
  margin-right: 16px;
  margin-left: 16px;
}

.download__text {
  font-weight: 300;
  margin-top: 26px;

  @media (min-width: @tablets) {
    font-size: 18px;
    line-height: 24px;
    margin-top: 38px;
  }

  @media (min-width: @desktops) {
    margin-top: 56px;
  }
}
